<template>
  <div class="container">
    <p><router-link to="/">返回</router-link></p>
    <div class="detailsBlog" v-if="!blogs.isCode">
      <h1 class="is-title">{{blogs.title}}</h1>
      <p >发布时间 &nbsp;{{blogs.isTime}}</p>
      <p><strong>内容：</strong> {{blogs.content}}</p>
      <p v-isColor><strong>分类：</strong> <span v-for="(item,index) in blogs.classIfy">{{ item }} {{ isComma(index,blogs.classIfy) }} </span> </p>
      <p v-isColor><strong>作者：</strong> <span v-for="(item,index) in blogs.author">{{ item }} {{ isComma(index,blogs.author) }} </span> </p>
    </div>
    <pre class="detailsBlog" v-else>
      <h1>{{blogs.title}}</h1>
      <p><strong>内容：</strong> {{blogs.content}}</p>
      <p v-isColor><strong>分类：</strong> <span v-for="(item,index) in blogs.classIfy">{{ item }} {{ isComma(index,blogs.classIfy) }} </span> </p>
      <p v-isColor><strong>作者：</strong> <span v-for="(item,index) in blogs.author">{{ item }} {{ isComma(index,blogs.author) }} </span> </p>
    </pre>
    <div class="btns">
      <button type="button" class="btn btn-primary editBlog"> <router-link :to="'/edit/'+num">编辑</router-link> </button>
      <button type="button" class="btn btn-danger" data-toggle="modal" data-target=".mtk">删除</button>
    </div>

    <!-- Small modal -->
    <div class="modal fade mtk" tabindex="-1" role="dialog">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
            <h4 class="modal-title">您正在进行删除操作！</h4>
          </div>
          <div class="modal-body">
            <p>确认删除？</p>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" data-dismiss="modal" @click="deleteBlog">确认</button>
          </div>
        </div><!-- /.modal-content -->
      </div><!-- /.modal-dialog -->
    </div><!-- /.modal -->
  </div>
</template>

<script>
export default {
  name:'detailsBlog',
  data(){
    return {
      num:this.$route.params.id,
      blogs:[]

    }
  },
  directives:{
    isColor:{
      bind(el,binding,vnode){
        el.style.color='#'+Math.random().toString(16).slice(6,12);
      }
    }

  },
  methods:{
    isComma(index,obj){
      if(index==obj.length-1){
        return ' ';
      }else if(!obj.length){
        return ' ';
      }else{
        return ','
      }
    },
    deleteBlog(){
      this.$http.delete("https://wd4437844136fkmeoc.wilddogio.com/myapp1/"+this.num+'.json').then(res=>{
        this.$router.push({path:'/'});
      })
    }
  },
  created(){
    this.$http.get("https://wd4437844136fkmeoc.wilddogio.com/myapp1/"+this.num+'.json').then(res=>{
      this.blogs=JSON.parse(res.bodyText);
    },err=>{
      alert('错误！！')
    })
  }
}
</script>

<style scoped>
.detailsBlog{
  padding:1px 10px 10px;
  box-sizing: 10px;
  background: #f2f2f2;
}
.editBlog a{
  color: #fff;
  text-decoration: none
}
.btns{
  margin-bottom: 30px;
}

</style>



