<template>

  <div class="container-fluid header-wrap">
    <div class="container">
      <ul class="hd">
        <li><router-link to="/" class="aa" exact>博客</router-link></li>
        <li><router-link to="/add" exact>添加博客</router-link></li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name:'blogHeader'
}
</script>

<style scoped>
textarea{resize: none}
a{text-decoration: none;}
li{list-style: none;}
ul,li{padding:0;margin:0}
.header-wrap{width: 100%;background:#d9534f;margin-bottom:20px}
.hd{overflow: hidden;box-sizing: border-box;padding:16px 0px;}
.hd li{float: left;}
.hd li+li{margin-left: 10px;}
.hd li a{display: block;padding:15px 20px;color: #fff;}
.hd li .router-link-active{background: rgb(249,201,212);border-radius: 5px;color: #000}
.hd li a:hover{background: rgb(249,201,212);border-radius: 5px;color: #000}
</style>



