<template>
  <div class="container">
    <div v-for="(blog,index) in blogs" :class="['list-blog',index % 2==0?'active':'active1']" >
      <h1 class="is-title">{{blog.title|to-uppercase}} </h1>
      <p >发布时间 &nbsp;{{blog.isTime}}</p>
      <p v-if="!blog.isCode"><strong>内容：</strong> {{sliceContent(blog.content)}}</p>
      <pre v-else><strong>内容：</strong> {{blog.content}} </pre>
      <p><router-link :to="'/blog/'+ blog.id">查看博客详情</router-link></p>
    </div>
  </div>
</template>

<script>
export default {
  name:'showBlog',
  data(){
    return {
      blogs:[]
    }
  },
  directives:{
    resBg:{
      bind(el,binding,vnode){
          el.style.color='#'+Math.random().toString(16).slice(2,8);
      }
    }
  },
  methods:{
    sliceContent(value){
      return  value.toString().length>50?value.toString().slice(0,50)+'...':value.toString();
    }
  },
  filters:{
    'to-uppercase'(value){
      return value.toString().charAt(0).toUpperCase()+value.slice(1)
    }
  },
  created(){
    this.$http.get('https://wd4437844136fkmeoc.wilddogio.com/myapp1.json').then(res=>{
      //this.blogs=res.body.slice(0,10);
      let isData=JSON.parse(res.bodyText);
      let dataArray=[];
      for(let key in isData){
        isData[key].id=key;
        dataArray.unshift(isData[key]);
      }
      this.blogs=dataArray;
    },err=>{
      alert('失败')
    })
  }
}
</script>

<style scoped>
.list-blog{
  padding:1px 20px 10px;
  box-sizing: border-box;
  margin-top: 20px;
}
.active{
  background: #f8f8f8;
}
.active1{
  background: #f2f2f2;
}
</style>



