
<template>
  <div id="myapp">

      <div class="container">

        <div class="myform" v-if="submited">
          <form class="" method="post">
              <div class="form-group">
                <label for="is-title">博客主题</label>
                <input type="text" name="" value="" class="form-control" placeholder="请输入博客主题" id="is-title" v-model="myTitle">
              </div>
              <div class="form-group">
                <label for="is-content">博客内容</label>
                <textarea name="name" rows="8" cols="80" class="form-control" placeholder="请输入博客内容" id="is-content" v-model="myContent"></textarea>

              </div>
              <div class="form-group">
                <p>
                  <label for="tf" class="checkbox-inline">
                    <input type="checkbox" v-model="isCode" id="tf">
                    是否启用源码方式展示内容？
                  </label>
                </p>
              </div>
              <div class="form-group">
                <label >分类</label>
                <div>
                  <label class="checkbox-inline" v-for="item in myClassifys1">
                    <input type="checkbox" :value="item" v-model="myClassifys">{{item}}
                  </label>

                </div>
              </div>
              <div class="form-group">
                <label for="is-content">作者</label>
                <select class="form-control" name="" v-model="myAuthors" multiple>
                  <option value="">请选择</option>
                  <option v-for="item in myAuthors1" :value="item">{{item}}</option>
                </select>
              </div>
              <div class="form-group">
                <button type="button" name="button" class="btn btn-danger" @click.prevent="blog">发表博客</button>
              </div>
          </form>
        </div>
        <p v-if="!submited"><router-link to="/">返回</router-link></p>
        <h4 v-if="!submited" class="text-success">博客发布成功</h4>
        <p><span>博客标题：</span><span>{{ myTitle }}</span></p>
        <p><span>博客内容：</span><span>{{ myContent }}</span></p>

        <p>
          <span>博客分类：</span>
          <span v-for="(classify,index) in myClassifys">{{classify}}{{isd1(index)}}
          </span>
        </p>
        <p><span>博客作者：</span><span v-for="(authodr,index) in myAuthors">{{ authodr }}{{ isd2(index) }}</span></p>
      </div>

  </div>
</template>

<script>
  export default{
    name:'myApp',
    data(){
      return {
        myTitle:'',
        myContent:'',
        myClassifys:[],
        myClassifys1:['html5','css3','Javascript','Vue','Angular','React'],
        myAuthors:[],
        myAuthors1:['小明','小红糖','小亚麻跌','水果','李四大哥','小扎扎'],
        submited:true,
        isCode:false
      }
    },
    methods:{
      isd1(index){
        return index==this.myClassifys.length-1?'':','
      },
      isd2(index){
        return index==this.myAuthors.length-1?'':','
      },
      blog(){
        this.$http.post('https://wd4437844136fkmeoc.wilddogio.com/myapp1.json',{
          title:this.myTitle,
          content:this.myContent,
          classIfy:this.myClassifys,
          author:this.myAuthors,
          isCode:this.isCode,
          isTime:this.isTime
        }).then(res=>{
          this.submited=false;
        },err=>{
          alert(err);
        })
      }
    },
    computed:{
      isTime(){
        const oDate=new Date()
        let oTime=oDate.getFullYear()+'-'+isdate(oDate.getMonth()+1)+'-'+isdate(oDate.getDate())+' '+isdate(oDate.getHours())+':'+isdate(oDate.getMinutes())+':'+isdate(oDate.getSeconds());
        function isdate(n){
          if(n>9){
            return n
          }else{
            return '0'+n
          }
        }
        return oTime;
      }
    },
    created(){

    }
  }

</script>

<style scoped>
textarea{resize: none}
a{text-decoration: none;}
li{list-style: none;}
ul,li{padding:0;margin:0}
.checkbox-inline{
  margin-left: 0;
  margin-right: 10px;
}
































</style>



